import axios from 'axios'
import { useLoadingStore } from '@/stores/loadingStore'
import { useUserStore } from '@/stores/userStore'
import router from '@/router'
import { $alert } from '@/utils/alertUtil'

// ‚úÖ Vue CLIÏóêÏÑúÎäî process.env.NODE_ENV ÏÇ¨Ïö©
const baseURL =
  process.env.NODE_ENV === 'production'
    ? '/prafta' // Ïö¥ÏòÅ ÏÑúÎ≤Ñ (Î∞∞Ìè¨ Ïãú nginx Îì±ÏóêÏÑú ÌîÑÎ°ùÏãúÎê®)
    : '/prafta' // Í∞úÎ∞ú ÏÑúÎ≤Ñ (vue.config.js proxy ÏÇ¨Ïö©)

const api = axios.create({
  baseURL, // ‚úÖ ÌîÑÎ°ùÏãúÎ•º ÌÜµÌï¥ HTTP ‚Üí HTTPS ÏûêÎèô Î≥ÄÌôò
  timeout: 5000,
  headers: {
    'Content-Type': 'application/json',
  },
})

// ÏöîÏ≤≠ Ïù∏ÌÑ∞ÏÖâÌÑ∞
api.interceptors.request.use(
  async (config) => {
    const loadingStore = useLoadingStore()
    loadingStore.startLoading()

    const userInfo = {
      gv_cmpnyCd: sessionStorage.getItem('gv_cmpnyCd'),
      gv_userId: sessionStorage.getItem('gv_userId'),
      gv_userNm: sessionStorage.getItem('gv_userNm'),
      gv_authCd: sessionStorage.getItem('gv_authCd'),
      gv_mblNo: sessionStorage.getItem('gv_mblNo'),
      gv_email: sessionStorage.getItem('gv_email'),
    }

    if (config.method === 'get') {
      config.params = { ...(config.params || {}), ...userInfo }
    } else if (!Array.isArray(config.data)) {
      config.data = { ...(config.data || {}), ...userInfo }
    }

    const token = sessionStorage.getItem('token')
    if (token) {
      config.headers.Authorization = `Bearer ${token}`
    }

    return config
  },
  (error) => {
    useLoadingStore().stopLoading()
    return Promise.reject(error)
  }
)

// ÏùëÎãµ Ïù∏ÌÑ∞ÏÖâÌÑ∞
api.interceptors.response.use(
  (response) => {
    useLoadingStore().stopLoading()
    return response
  },
  (error) => {
    useLoadingStore().stopLoading()
    const userStore = useUserStore()

    if (error.status === '404' && error.response?.data?.message === 'Ïú†Ìö®ÌïòÏßÄ ÏïäÏùÄ ÌÜ†ÌÅ∞ÏûÖÎãàÎã§.') {
      $alert(error.response.data.message)
      sessionStorage.clear()
      userStore.logout()
      router.push('/')
      return new Promise(() => {}) // ÏöîÏ≤≠ Ï§ëÎã®
    }

    return Promise.reject(error)
  }
)

export default api
